.root {
  align-self: center;
  margin: 20px 40px;
  display: flex;

  li {
    display: inline-block;
    a {
      margin-left: 10px;
      height: 40px;
      line-height: 38px;

      width: 40px;
      border-radius: 100%;
      cursor: pointer;
      border: solid 2px #fe5f1e;
      display: inline-block;
      text-align: center;
      &:hover {
        background: #fe5f1e;
        color: #fff;
      }
    }
  }
  :global(.selected) {
    a {
      background: #fe5f1e;
      color: #fff;
    }
  }
}
@media (max-width: 360px) {
  .root {
    margin: 20px 3px;
  }
}
