.sort {
  position: relative;
}
.label {
  display: flex;
  align-items: center;

  svg {
    cursor: pointer;
    margin-right: 8px;
    transition: all 0.2s ease-in-out;
  }

  b {
    margin-right: 8px;
  }

  span {
    color: #fe5f1e;
    border-bottom: 1px dashed #fe5f1e;
    cursor: pointer;
  }
}
.popup {
  position: absolute;
  right: 0;
  margin-top: 15px;
  background: #ffffff;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.09);
  border-radius: 10px;
  overflow: hidden;
  padding: 10px 0;
  width: 160px;

  ul {
    overflow: hidden;
    li {
      padding: 12px 20px;
      cursor: pointer;

      &.active,
      &:hover {
        background: rgba(252, 162, 127, 0.05);
      }
    }
  }
}
.rotate {
  transform: rotate(180deg);
}
.active {
  font-weight: bold;
  color: #fe5f1e;
}
